@font-face {
  font-family: "Lato-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  object-fit: cover;
  height: auto;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  margin: 0;
  height: 1px;
}

.page__body {
  margin: 0;
  padding: 0;
  color: #444444;
  font-family: "Lato-Regular", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
@media screen and (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}

main {
  flex: 1;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 110px;
    padding-right: 110px;
  }
}

.title {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  color: #000000;
  line-height: 26px;
}
@media screen and (min-width: 768px) {
  .title {
    font-size: 36px;
    line-height: 36px;
  }
}

.heading {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
  margin: 0;
  padding: 0;
}
@media screen and (min-width: 768px) {
  .heading {
    font-size: 60px;
    line-height: 60px;
  }
}

.button {
  display: block;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  cursor: pointer;
  outline: none;
}
@media screen and (min-width: 1440px) {
  .button {
    font-size: 20px;
    line-height: 26px;
  }
}

.button--primary {
  background-color: #68b738;
  color: #ffffff;
}
.button--primary:hover, .button--primary:focus {
  background-color: #5eaa2f;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}
.button--secondary:hover, .button--secondary:focus {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}

.input-text {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-style: normal;
  line-height: 18px;
  text-transform: uppercase;
  padding: 13px 35px 13px 16px;
  border: 1px solid #e7e7e7;
  appearance: textfield;
  flex-grow: 1;
  width: calc(50% - 13px);
}
.input-text::placeholder {
  color: #444444;
  opacity: 1;
}
@media screen and (min-width: 768px) {
  .input-text {
    font-size: 20px;
  }
}
.input-text::-webkit-outer-spin-button, .input-text::-webkit-inner-spin-button {
  appearance: none;
}
.input-text:hover:not(:disabled) {
  border-color: #cdcdcd;
}
.input-text:focus {
  border-color: #444444;
  outline: 1px solid #444444;
}
.input-text:not(:placeholder-shown):invalid {
  border-color: #ff8282;
  outline: 1px solid #ff8282;
}

.main-header {
  position: relative;
  border-bottom: 1px solid #f2f2f2;
  padding-top: 7px;
  padding-bottom: 7px;
}
@media screen and (min-width: 768px) {
  .main-header {
    padding-top: 24px;
    border: none;
  }
}
@media screen and (min-width: 1440px) {
  .main-header {
    padding-top: 56px;
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 56px;
  flex-wrap: wrap;
  min-height: 53px;
}
@media screen and (min-width: 768px) {
  .main-header__wrapper {
    justify-content: space-between;
    gap: 36px;
  }
}

@media screen and (min-width: 768px) {
  .main-header__link-text {
    display: none;
  }
}

@media screen and (min-width: 1440px) {
  .main-header__link-image:hover {
    opacity: 0.8;
  }
  .main-header__link-image:active {
    opacity: 0.6;
  }
}

.no-js .main-header__toggle {
  display: none;
}

.main-header__toggle {
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/menu.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-header__toggle--closed {
  background-image: url("../img/icons/close.svg");
}
@media screen and (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.no-js .main-nav {
  display: block;
}

.main-nav {
  position: absolute;
  z-index: 10;
  top: 68px;
  left: 0;
  width: 100%;
  display: none;
}
.main-nav--opened {
  display: block;
}
@media screen and (min-width: 768px) {
  .main-nav {
    position: static;
    display: block;
    width: auto;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
}
@media screen and (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 34px;
    background-color: transparent;
    margin-right: 4px;
  }
}
@media screen and (min-width: 1440px) {
  .main-nav__list {
    gap: 34px;
    margin-right: 0;
  }
}

.main-nav__item {
  border-top: 1px solid #e6e6e6;
  width: 100%;
  text-align: center;
}
.main-nav__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
@media screen and (min-width: 768px) {
  .main-nav__item {
    border: none;
    width: auto;
  }
  .main-nav__item:last-child {
    border: none;
  }
}

.main-nav__link {
  display: block;
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  padding-top: 22px;
  padding-bottom: 22px;
}
@media screen and (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
    padding-top: 9px;
    padding-bottom: 9px;
  }
}
@media screen and (min-width: 1440px) {
  .main-nav__link {
    font-size: 20px;
  }
}
.main-nav__link:hover, .main-nav__link:focus {
  outline: none;
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}

.main-nav__link--current {
  position: relative;
}
@media screen and (min-width: 768px) {
  .main-nav__link--current::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color: #68b738;
    bottom: 0;
    display: block;
  }
}
@media screen and (min-width: 1440px) {
  .main-nav__link--current::after {
    background-color: #68b738;
  }
}
.main-nav__link--current:hover, .main-nav__link--current:active {
  opacity: 1;
}

@media screen and (min-width: 1440px) {
  .main-nav__link--index {
    color: #ffffff;
  }
  .main-nav__link--index::after {
    background-color: #ffffff;
  }
}

@media screen and (min-width: 768px) {
  .footer {
    padding-top: 66px;
    padding-bottom: 66px;
  }
}
@media screen and (min-width: 1440px) {
  .footer {
    padding-top: 70px;
  }
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .footer__wrapper {
    display: grid;
    grid-template-columns: 1fr 135px 1fr;
    column-gap: 100px;
  }
}

.footer__logo-link {
  border-bottom: 1px solid #d9d9d9;
  width: 100%;
  display: block;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 14px;
}
@media screen and (min-width: 768px) {
  .footer__logo-link {
    text-align: left;
    border: none;
    padding: 0;
  }
}

.footer__socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  max-width: 135px;
  gap: 28px;
  margin-right: 3px;
  list-style: none;
  padding: 23px 0 20px;
}

.footer__icon {
  color: #666666;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__developer {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  border-top: 1px solid #d9d9d9;
  padding-top: 20px;
  padding-bottom: 40px;
}
@media screen and (min-width: 768px) {
  .footer__developer {
    justify-content: flex-end;
    column-gap: 23px;
    border: none;
    padding: 0;
  }
}

.footer__developer-link {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  font-family: "Lato-Regular", sans-serif;
  color: #444444;
  text-decoration: none;
}
.footer__developer-link:hover, .footer__developer-link:focus {
  color: #68b738;
}
.footer__developer-link:active {
  opacity: 0.3;
}

.footer__link:focus {
  outline: none;
}

.footer__link:hover .footer__icon,
.footer__link:focus .footer__icon {
  color: #68b738;
}

.footer__link:active .footer__icon {
  opacity: 0.3;
}

.page-main__promo {
  min-height: 480px;
}

.page-catalog__assortment {
  padding-top: 25px;
  max-width: initial;
  padding-left: 0;
