}
@media screen and (min-width: 768px) {
  .page-catalog__assortment {
    max-width: 768px;
    padding-top: 55px;
    padding-left: 29px;
    padding-right: 29px;
  }
}
@media screen and (min-width: 1440px) {
  .page-catalog__assortment {
    max-width: 1440px;
    padding-left: 110px;
    padding-right: 110px;
    padding-top: 60px;
    padding-bottom: 70px;
  }
}

.address {
