.advantages {
  padding-top: 20px;
  padding-bottom: 35px;
}
@media screen and (min-width: 768px) {
  .advantages {
    padding-top: 26px;
  }
}
@media screen and (min-width: 1440px) {
  .advantages {
    padding-top: 72px;
  }
}

.advantages__heading {
  margin-bottom: 42px;
}
@media screen and (min-width: 768px) {
  .advantages__heading {
    margin-bottom: 50px;
  }
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
  counter-reset: section;
}
@media screen and (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 92px 61px;
  }
}
@media screen and (min-width: 1440px) {
  .advantages__list {
    grid-template-columns: repeat(4, 1fr);
    gap: 94px 77px;
  }
}

.advantages__item {
  padding-left: 80px;
  position: relative;
  z-index: 7;
}
@media screen and (min-width: 768px) {
  .advantages__item {
    padding: 133px 0 20px;
  }
  .advantages__item::after {
    content: counter(section);
    counter-increment: section;
    display: block;
    position: absolute;
    font-size: 280px;
    color: #f2f2f2;
    font-family: "Oswald", sans-serif;
    z-index: -1;
    top: 110px;
    right: 75px;
  }
}
@media screen and (min-width: 1440px) {
  .advantages__item::after {
    right: 0;
  }
}
.advantages__item::before {
  content: "";
  position: absolute;
  display: block;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
  top: 0;
  left: 0;
}
@media screen and (min-width: 768px) {
  .advantages__item::before {
    width: 80px;
    height: 80px;
    top: 20px;
  }
}
.advantages__item--leaf::before {
  background-image: url("../img/icons/leaf.svg");
}
.advantages__item--glass::before {
  background-image: url("../img/icons/glass.svg");
}
.advantages__item--cutlery::before {
  background-image: url("../img/icons/cutlery.svg");
}
.advantages__item--time::before {
  background-image: url("../img/icons/clock.svg");
}

.advantages__text {
  margin: 0;
  padding: 0;
}
@media screen and (min-width: 768px) {
  .advantages__text {
    max-width: 265px;
  }
}

