@media screen and (min-width: 768px) {
  .additional-card__link {
    padding: 7px 220px;
    grid-column: 1/-1;
  }
}
@media screen and (min-width: 1440px) {
  .additional-card__link {
    padding: 8px 26px;
    grid-column: 3/4;
  }
}

.text-label {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-style: normal;
  line-height: 18px;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .text-label {
    font-size: 20px;
    line-height: 30px;
  }
}

.pick__label {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.pick__mark {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
.pick__mark--radio {
  border-radius: 50%;
}
@media screen and (min-width: 768px) {
  .pick__mark {
    width: 40px;
    height: 40px;
  }
}

.pick__input:checked + .pick__mark--radio::before {
  content: "";
  display: block;
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
}
@media screen and (min-width: 768px) {
  .pick__input:checked + .pick__mark--radio::before {
    width: 18px;
    height: 18px;
  }
}

.pick__input:checked + .pick__mark--checkbox::before {
  content: "";
  position: absolute;
  display: block;
  width: 26px;
  height: 20px;
  background: url("../img/icons/check.svg") no-repeat;
}

.pick__name {
  padding-left: 16px;
}

.pick__input:hover ~ .pick__mark,
.pick__input:focus ~ .pick__mark {
  border-color: #bababa;
}

.pick__input:disabled + .pick__mark,
.pick__input:disabled ~ .pick__name,
.pick__input:disabled ~ .pick__label {
  opacity: 0.3;
  cursor: default;
}

.decoration-line {
  position: relative;
  display: flex;
  justify-content: center;
}
.decoration-line::before {
  content: "";
  position: absolute;
  left: 0;
  transform: translateY(50%);
  width: 100%;
  height: 10px;
  border-bottom: 2px solid #68b738;
}
.decoration-line::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 10px;
  transform: translateY(50%);
  right: 0;
  border-bottom: 2px solid #68b738;
}

.decoration-line__title {
  width: 100%;
  z-index: 1;
  position: relative;
}
.decoration-line__title::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 100%;
  background-color: #ffffff;
  transform: translateX(-100%);
}

.decoration-line__text {
  display: inline-block;
  background-color: #ffffff;
  padding-right: 20px;
  max-width: 250px;
}
@media screen and (min-width: 768px) {
  .decoration-line__text {
    width: auto;
    padding-right: 20px;
  }
}

.slider {
  position: relative;
  width: 280px;
  height: 256px;
  --position: 50%;
}
@media screen and (min-width: 768px) {
  .slider {
    width: 560px;
    height: 512px;
  }
}

.slider__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider__image-wrapper {
  position: absolute;
  width: var(--position);
  height: 100%;
}

.image-before {
  object-position: left;
}

.image-after {
  object-position: right;
}

.image-wrapper-after {
  right: 0;
  width: calc(100% - var(--position));
}

.slider__input {
  position: absolute;
  inset: 0;
  cursor: pointer;
  opacity: 0;
  width: 100%;
  height: 100%;
}

.slider__input:focus-visible ~ .slider__button {
  outline: 5px solid #000000;
  outline-offset: 3px;
}

.slider__line {
  position: absolute;
  width: 4px;
  height: 100%;
  background-color: #666666;
  left: var(--position);
  transform: translateX(-50%);
  pointer-events: none;
}

.slider__button {
  color: #666666;
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: var(--position);
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.slider__button:hover {
  color: #f2f2f2;
}

.slider__button:active {
  color: #5eaa2f;
}